@import "src/global-styles/variables";

.wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
}

.name {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.value {
  height: 24px;
  padding: 0 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 14px;
  border-radius: 4px;
  line-height: 20px;
  background: $main-text;
  color: $white;
}

.highlighted {
  background: $primary-back;
}
