@import "src/global-styles/variables";

.events {
  display: grid;
  justify-content: center;
  gap: 8px;
}

.event {
  //width: max-content;
  padding: 16px 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border: 1px solid $secondary-border;
}

.header {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}

.body {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

