@import "src/global-styles/variables";
@import "src/global-styles/mixins";

.wrapper {
  position: relative;
  display: flex;
  align-items: stretch;
}

.input {
  flex-grow: 1;
  padding: 12px 60px 12px 16px;
  width: 100%;
  background-color: $field-back;
  border-radius: 8px;
  outline: none;
  border: 1px solid transparent;

  &:hover {
    border-color: $disabled-text;
  }

  &:focus {
    border-color: $main-text;
    background-color: $white;
  }
}

.isError {
  input {
    border: 1px solid $red !important;
  }
}

.isSearch {
  //padding: 12px 48px 12px 16px;
}

.aside {
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
}

.name {
  margin-bottom: 12px;
}

.searchIcon {
  @extend .aside;
}

.currency {
  @extend .aside;
  color: $annotation-text;
}

.right-icon {
  position: absolute;
  right: 16px;
  top: 50%;
  display: inline-flex;
  align-items: center;
  transform: translateY(-50%);
}

.textarea {
  @include open-16;

  min-height: 100px;
}

.counter {
  margin-top: 12px;
  color: $annotation-text;
}

.error {
  margin-top: 8px;
  color: $red;
}

@media screen and (max-width: 768px) {
  .name {
    margin-bottom: 8px;
  }
}
