@mixin color-svg($color) {
  path:not([ignore-fill="true"]) {
    fill: $color;
  }
  path:not([ignore-stroke="true"]) {
    stroke: $color;
  }
}

@mixin open-24 {
  font-family: $font-family;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;

  @media screen and (max-width: 768px) {
    font-size: 16px;
    line-height: 24px;
  }
}

