@import "src/global-styles/variables";

.wrapper {
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 16px;
}

.track {
  width: 60px;
  height: 32px;
  border-radius: 24px;
  position: relative;
  transition: all ease .25s;
  background-color: $secondary-border;
}

.thumb {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  transition: all ease .25s;
  background-color: $white;
}


.active {
  .track {
    background-color: $primary-back;
  }

  .thumb {
    transform: translateX(28px);
  }
}

