@import "src/global-styles/variables";

.paginator {
  display: flex;
  justify-content: center;
}

.page {
  min-width: 36px;
  height: 38px;
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: $primary-back;
  background-color: $white;
  border: 1px solid $secondary-border;
  transition: all .3s ease;
  cursor: pointer;

  &:hover {
    background-color: $primary-hover-back;
    color: $white;
  }

  &:active {
    background-color: $primary-active-back;
    color: $white;
  }

  &:disabled {
    color: $disabled-text;
    background-color: $white;
    cursor: default;
  }
}

.page.selected {
  background-color: $primary-hover-back;
  color: $white;
}
