@import "src/global-styles/variables";

.explorerPage {
  padding: 32px 40px 36px;
}

.caption {
  display: grid;
  grid-template: "heading1 _ search" auto
                 "info info info" auto
                 "heading2 __ toggle" auto
                / auto 1fr auto;
}
.icon{
  grid-area: heading1;
  
}
.heading1, .heading2 {
  font-weight: 400;
}

.heading1 {
  grid-area: heading1;
  font-size: 20px;
  margin-left: 60px;
  line-height: 28px;
  align-self: center;
}

.heading2 {
  grid-area: heading2;
  margin-top: 36px;
  font-size: 16px;
  line-height: 24px;
  align-self: center;
}

.searchInput {
  grid-area: search;
  max-width: 376px;
  flex-grow: 1;
}

.info {
  grid-area: info;
  margin-top: 24px;
  padding: 15px 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 44px;
  border: 1px solid $secondary-border;
}

.toggle {
  grid-area: toggle;
  margin-top: 32px;
  justify-content: end;
}

.blocksTable {
  margin-top: 24px;
}

.paginator {
  margin-top: 36px;
}

@media (max-width: 915px) {
  .info {
    justify-content: center;
  }
}

@media (max-width: 650px) {
  .explorerPage {
    padding-right: 20px;
    padding-left: 20px;
  }
}

@media (max-width: 975px) {
  .caption {
    grid-template: "heading1 heading1" auto
                   "info info" auto
                   "heading2 search" auto
                   "toggle toggle" auto
                   / auto 1fr;
  }

  .info {
    margin: 12px 0 24px;
    row-gap: 10px;
  }

  .heading2 {
    margin: 0;
  }
  .searchInput {
    justify-self: end;
  }

  .toggle {
    justify-self: start;
  }
}

@media (max-width: 440px) {
  .caption {
    grid-template: "heading1" auto
                   "info" auto
                   "heading2" auto
                   "search" auto
                   "toggle" auto
                   / auto;
  }

  .heading2 {
    text-align: center;
  }

  .searchInput {
    margin-top: 14px;
    justify-self: center;
  }

  .toggle {
    margin-top: 20px;
    justify-self: center;
  }
}

@media (max-width: 400px) {
  .heading1 {
    text-align: center;
  }
