@import "src/global-styles/variables";

.table {
  width: 100%;
  display: table;
  margin-top: 20px;
  margin-left: 5px;
}
.linkTable{
  color: #0070C9;
  cursor: pointer;
}
.header {
  background-color: $field-back;
}
.mobile{
  display: none;
}
.row {
  display: table-row;
  width: 100%;
}

.row:not(.header) {
  box-shadow: inset 0px -1px 0px #E8E8E8;
}

.cell {
  display: table-cell;
  padding: 16px 20px;
}

.cards {
  display: none
}

@media (max-width: 1024px) {
  .cell{
    word-break: break-word;
    padding: 16px 0;
  }
  .value{
    font-weight: 700;
  }
  .head{
    display: none;
  }
  .table{
    font-size: 14px;
    margin-left: 0;
  }
  .attribute{
    word-break: normal;    
  }
}


.cards {
  justify-content: center;
  gap: 8px;
}

.card {
  padding: 16px 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  border: 1px solid $secondary-border;
  border-radius: 8px;
  font-size: 16px;
  line-height: 20px;
}

.caption {
  display: flex;
  gap: 12px;
}

.type {
  flex-grow: 1;
}

.info {
  display: flex;
  justify-content: space-between;
  gap: 6px;
}

.age, .size {
  display: flex;
  align-items: center;
  text-align: center;
  gap: 9px;
}

.value {
  //width: 94px;
}

.timeIcon {
  width: 20px;
  height: 20px;
}

.diamondIcon {
  width: 20px;
  height: 20px;
}
