@import "src/global-styles/variables";

.table {
  width: 100%;
  display: table;
  margin-top: 20px;
  margin-left: 5px;
  white-space: pre-wrap;
}
.linkTable{
  color: #0070C9;
}

.header {
  background-color: $field-back;
}

.row {
  display: table-row;
  width: 100%;
}

.row:not(.header) {
  box-shadow: inset 0px -1px 0px #E8E8E8;
}

.cell {
  display: table-cell;
  white-space: pre-wrap;
  padding: 16px 20px;
}
.code{
  word-break:break-all;
  white-space: pre-wrap;
}
.cards {
  display: none
}
@media (max-width: 1024px) {
  .cell{
    word-break: break-word;
    padding: 16px 5px;
  }
  .value{
    font-weight: 700;
  }
  .head{
    display: none;
  }
  .attribute{
    word-break: normal;
  }
  .table{
    font-size: 14px;
    margin-left: 0;
  }
}

