@import "src/global-styles/variables";

.table {
  width: 100%;
  display: table;
}

.header {
  background-color: $field-back;
}

.row {
  display: table-row;
  width: 100%;
}

.redirectBtn {
  cursor: pointer;
}

.row:not(.header) {
  box-shadow: inset 0px -1px 0px #E8E8E8;
}

.cell {
  display: table-cell;
  padding: 16px 20px;
}

.cards {
  display: none
}

@media (max-width: 1000px) {
  .table {
    display: none;
  }
  .cards {
    display: grid;
  }
}



.cards {
  justify-content: center;
  gap: 8px;
}

.card {
  padding: 16px 20px;
  display: flex;
  flex-wrap: wrap; 
  min-width: 413px;
  align-items: center;
  gap: 12px;
  border: 1px solid $secondary-border;
  border-radius: 8px;
  font-size: 14px;
  line-height: 20px;
}

@media (max-width: 462px) {
  .card{
    min-width: 0px;
    width: 294px;
  }
}

.caption {
  display: flex;
  gap: 12px;
}

.type {
  flex-grow: 1;
}

.info {
  display: flex;
  justify-content: space-between;
  gap: 6px;
}

.age, .size {
  display: flex;
  align-items: center;
  text-align: center;
  gap: 9px;
}

.value {
  //width: 94px;
}

.timeIcon {
  width: 20px;
  height: 20px;
}

.diamondIcon {
  width: 20px;
  height: 20px;
}
