::-webkit-scrollbar {
  width: 8px;
  background-color: $secondary-disabled-back;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background-color: $secondary-border;
  width: 6px;
  border-radius: 4px;

  &:hover {
    background-color: rgb(66, 66, 66);
  }
}
