@import "src/global-styles/variables";

.tabs {
  padding: 0 32px;
  display: flex;
  gap: 27px;
  border-radius: 8px;

  &_button {
    display: inline-flex;
    padding: 8px 4px;
    background: $field-back;
    gap: 8px;
  }

  &_fullButton {
    padding: 0;
    border-radius: 8px;
    overflow: hidden;
    gap: 1px;
  }
}

.tab {
  padding: 20px 0;
  position: relative;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: $pale-icon;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    color: $main-text;
  }

  &.active {
    color: $main-text;
  }

  &_button {
    padding: 8px 16px;
    border-radius: 8px;
    color: $annotation-text;

    &.active {
      background: $primary-back;
      color: $white;
    }
  }

  &_fullButton {
    text-align: center;
    padding-top: 12px;
    padding-bottom: 12px;
    width: 100%;
    background: $field-back;
    color: $annotation-text;

    &.active {
      background: $primary-back;
      color: $white;
    }
  }
}

.tab.isAllWidthTabs {
  flex-grow: 1;
  text-align: center;
}

.isDisabled {
  color: $disabled-text;
  pointer-events: none;
  cursor: none;
}

.smallTab {
  padding: 11px 0;
}

.selectedTabUnderline {
  position: absolute;
  height: 2px;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background-color: $primary-back;
}

@media screen and (max-width: 768px) {
  .tab {
    &_fullButton {
      padding-top: 8px;
      padding-bottom: 8px;
    }
  }
}

