@import "variables";
@import "reset";
@import "fonts";
@import "layout";
@import "scroll";

h1 {
  line-height: 30px;
}

h1, h2 {
  font-weight: 400;
}


.annotation {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $annotation-text;
}

.textCenter {
  text-align: center;
}

.bold {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #181818;
}

.small {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.whiteCard {
  background: #FFFFFF;
  border-radius: 12px;
}

a {
  color: $primary-back;

  &:hover {
    color: $primary-hover-back;
  }

  &:active {
    color: $primary-active-back;
  }
}

button.vanished {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

.reverse {
  transform: rotate(180deg);
}

hr {
  margin: 24px 0;
  height: 0;
  border: 1px solid $secondary-disabled-back;
}

input, label {
  font-size: 16px;
  line-height: 24px;
}

input::placeholder {
  color: $annotation-text;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}
